.feed {
    flex: 0.4 1;
    /* border-right: black 1px solid; */
    border-right: var(--twitter-background) 1px solid;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    overflow-x: scroll;
}

.feed__header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--twitter-background);
    padding: 15px 20px;
}


/* Hide scrollbar for chrome, safari and opera */

.feed::-webkit-scrollbar {
    display: none;
}


/* Hide scrollbar for IE , Edge and Firefox */

.feed {
    -ms-overflow-style: none;
    /*IE and Edge*/
    scrollbar-width: none;
    /*Firefox*/
}

.feed__header>h2 {
    font-size: 20px;
    font-weight: 700;
}
.tweetBox {
    padding-bottom: 10px;
    border-bottom: 8px solid var(--twitter-background);
    padding-right: 10px;
}

.tweetBox>form {
    display: flex;
    flex-direction: column;
}

.tweetBox__input {
    display: flex;
    padding: 20px;
}

.tweetBox__input>input {
    flex: 1 1;
    margin-left: 20px;
    font-size: 20px;
    border: none;
}

.tweetBox__input>input:focus {
    outline: none;
}

.tweetBox__button {
    background-color: var(--twitter-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 700 !important;
    text-transform: inherit !important;
    border-radius: 20px !important;
    height: 45px !important;
    margin-top: 20px !important;
    margin-left: auto !important;
}

.tweetBox__imageInput {
    border: none;
    padding: 15px;
}
.post__body>img {
    border-radius: 20px;
    width: 100%;
}

.post {
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--twitter-backgroung);
}

.post__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.post__badge {
    font-size: 14px !important;
    color: var(--twitter-custom-color);
    /* color: var(--twitter-color); */
}

.post__headerSpecial {
    font-weight: 600;
    font-size: 12px;
    color: gray;
}

.post__headerText>h3 {
    font-size: 15px;
    margin-bottom: 5px;
}

.post__headerDescription {
    font-size: 15px;
    margin-bottom: 20px;
}

.post__body {
    flex: 1 1;
    padding: 10px;
}

.post__avatar {
    padding: 20px;
}
.sidebar__tweet {
    background-color: var(--twitter-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 700 !important;
    text-transform: inherit !important;
    border-radius: 20px !important;
    height: 50px !important;
    margin-top: 20px !important;
}

.sidebar {
    border-right: 1px solid var(--twitter-color);
    flex: 0.25 1;
    min-width: 250px;
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
}

.sidebar__twitterIcon {
    /* color: #50b7f5; */
    color: var(--twitter-color);
    font-size: 30px !important;
    margin-left: 20px;
    margin-bottom: 20px;
}
.sidebarOption {
    display: flex;
    align-items: center;
    cursor: pointer;
    border-radius: 30px;
}

.sidebarOption:hover {
    background-color: #e8f5fe;
    color: var(--twitter-color);
    transition: color 100ms ease-out;
}

.sidebarOption>.MuiSvgIcon-root {
    padding: 20px;
}

.sidebarOption>h2 {
    font-weight: 700;
    font-size: 20px;
    margin-right: 20px;
}

.sidebarOption--active {
    color: var(--twitter-color);
}
.widgets {
    flex: 0.3 1;
    overflow-y: scroll;
}

.widgets::-webkit-scrollbar {
    display: none;
}

.widgets__input {
    display: flex;
    align-items: center;
    background-color: var(--twitter-background);
    padding: 10px;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 20px;
}

.widgets__input>input {
    border: none;
    background-color: var(--twitter-background);
}

.widgets__input>input:focus {
    outline: none;
}

.widgets__searchIcon {
    color: gray;
}

.widgets__widgetContainer {
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    background-color: #f5f8fa;
    border-radius: 20px;
}

.widgets__widgetContainer>h2 {
    font-size: 18px;
    font-weight: 700;
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html,
body,
#root {
    min-width: 100%;
}

body {
    background: rgba(0, 0, 0, 0.5) url(/static/media/background.a89e1ec7.jpg) no-repeat;
    background-size: cover;
    -webkit-font-smoothing: antialiased !important;
}

body,
input,
button {
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-size: 14px;
}

.container {
    margin: 80px auto;
    max-width: 450px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container h1 {
    color: #f05a5b;
    font-size: 36px;
}

.content {
    width: 80%;
    opacity: 0.8;
    background: #ffffff;
    margin-top: 50px;
    border-radius: 4px;
    padding: 30px;
}

.content>p {
    font-size: 22px;
    line-height: 30px;
    margin-bottom: 30px;
}

.content button.submit-btn {
    border: 0;
    border-radius: 2px;
    width: 100%;
    height: 42px;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 700;
    background: #f05a5b;
    color: #ffffff;
    cursor: pointer;
}

.content button.submit-btn:hover {
    background: #e14f50;
    opacity: 1;
}

.content button.secondary-btn {
    border: 0;
    border-radius: 2px;
    width: 100%;
    height: 42px;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 700;
    background: #337ab7;
    color: #ffffff;
    cursor: pointer;
}

.content button.secondary-btn:hover {
    background: #286090;
    opacity: 1;
}

.content div.input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}
ul.events-list {
    width: 100%;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    margin-bottom: 30px;
}

ul.events-list li {
    display: flex;
    flex-direction: column;
}

ul.events-list li header {
    width: 100%;
    height: 240px;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 4px;
    /* width: 100%;
    height: 240px;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 4px; */
}

ul.events-list li strong {
    margin-top: 12px;
    font-size: 24px;
    color: #444;
}

div.filter-panel {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}

ul.events-list header div {
    display: flex;
    flex-direction: row-reverse;
}

ul.notifications {
    list-style: none;
    margin-bottom: 16px;
    /* display: flex;
    justify-content: space-between; */
}

ul.notifications li {
    font-size: 16px;
    line-height: 24px;
}

@media (max-width:768px) {
    ul.events-list {
        grid-template-columns: 1fr;
    }
}
label#thumbnail {
    margin-bottom: 20px;
    border: 1px;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 120px;
}

label#thumbnail input {
    display: none;
}

label#thumbnail.has-thumbnail {
    border: 0;
}

label#thumbnail.has-thumbnail img {
    display: none;
}

.container > .event-validation {
    margin-top: 12px;
}

.events {
    list-style: none;
    display: flex;
    flex-direction: column;
}

.events li {
    padding: 30px;
    margin: 1rem;
    border-radius: 4px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.events div {
    margin-top: 0.5rem;
}

.events-details span {
    margin: 0.5rem;
}

.Approved {
    color: green;
}

.Rejected {
    color: red;
}

.Pending {
    color: blue;
}

